import React from 'react';
import SkillItem from './skill-item';

// create a component
const Skillset = ({title, skills, isMobile:matches}) => {
    return (
        <div style={{display:'inline-block', margin:10}}>
          <h2 style={{color:'white'}}>{title}</h2>
          <div style={{width:matches?'auto':'clamp(300px, 33vw, 500px)', display:'flex', flexWrap:'wrap', flexGrow:1, justifyContent:'stretch'}}>
            {skills.map(skill => <SkillItem skill={skill} />)}
          </div>
        </div>
    );
};


export default Skillset;
