import mail from '../envelope-solid.svg'
import github from '../github-brands.svg'
import download from '../download-solid.svg'
import linkedin from '../linkedin-brands.svg'
import phone from '../phone-alt-solid(1).svg'
import React from 'react'

const Contact = ({info}) => {



    const icon = info.includes('@')?mail:
                info.includes('https')?info.includes('github')?github:info.includes('linkedin')?linkedin:download
                :phone

    const addit = info.includes('https')?'':
            info.includes('@')?'mailto:':'tel:'
    if(addit){
        return (
            <a href={`${addit}${info}`} >
                <img src={icon} href="tel:+2348138754586" width="30px" style={{color:'white', padding:10}} />
            </a>
        );
    }else{
        return (
            <a href={`${addit}${info}`} target="_blank" rel="noreferrer">
                <img src={icon} href="tel:+2348138754586" width="30px" style={{color:'white', padding:10}} />
            </a>
        );
    }
}

export default Contact;