import github from '../github-brands.svg'
import link from '../external-link-alt-solid.svg'
import useDevice from '../customhooks/detectDevice'


const Project = ({project}) => {

    const phone = useDevice(400);

    const tablet = useDevice(768);

    return (
        <div key={project.id} style={{backgroundColor:'rgba(0, 0, 0, .7)', display:'flex', overflow:'hidden', alignItems:'center', position:"relative", borderRadius:6, width:300, height:200, color:'white', margin:phone?'auto':tablet?15:20, marginBottom:30}}>
            <div style={{transform:'skew(20deg)', color:'#fff', backgroundColor:'#ffffff33', display:'inline-block', position:'absolute', top:0, left:-20, width:'auto', padding:8, paddingLeft:30, paddingRight:15}}><h2 style={{margin:0, transform:'skew(-20deg)'}}>{project.title}</h2></div>
            <div style={{top:10, right: 10, width:'auto', position:'absolute'}}>
                <a style={{paddingLeft:15}} href={project.github} target="_blank" rel="noreferrer"><img src={github} width="20px" /></a>
                <a style={{paddingLeft:15}} href={project.url} target="_blank" rel="noreferrer"><img src={link} width="20px" /></a>
            </div>
            <h4 style={{padding:20, display:'block'}}>
                {project.description}
            </h4>
            <div style={{width:'100%', position:'absolute', bottom:0, paddingLeft:20, boxSizing:'border-box'}}>
                <h4 style={{display:'inline-block'}}>Tags:</h4>
                <div style={{display:'inline-flex', flexFlow:'row wrap', width:'70%', height:'auto', paddingRight:20}}>
                    {project.tags.map(tag => <p style={{display:'inline-block', margin:5, padding:'1px 5px 1px 5px', borderRadius:4, color:'black', backgroundColor:'ButtonFace'}}>{tag}</p>)}
                </div>
            </div>
        </div>
    );
}

export default Project;